import React from "react";
import { App } from "./App";

export class AppContainer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            output: ""
        };
        this.handleChange = this.handleChange.bind(this);
    }
    math(a, b, c) {
        let D = b * b - 4 * (a * c);
        let Dsqrt = Math.sqrt(b * b - 4 * (a * c));
        console.log(D);
        if (D < 0) {
            this.setState({ output: "Дискриминант меньше нуля йопта" });
        } else {
            let x1 = (-b + Dsqrt) / (2 * a);
            let x2 = (-b - Dsqrt) / (2 * a);
            if (Number.isInteger(x1) !== true || Number.isInteger(x2) !== true) {
                this.setState({ output: `X1 ≈ ${x1.toFixed(2)}  X2 ≈ ${x2.toFixed(2)}` });
            } else {
                this.setState({ output: `X1 = ${x1}  X2 = ${x2}` });
            }

        }
    }
    handleChange() {
        let a = parseInt(document.getElementById("a").value, 10);
        let b = parseInt(document.getElementById("b").value, 10);
        let c = parseInt(document.getElementById("c").value, 10);
        console.log(a);
        console.log(b);
        console.log(c);
        if (a !== 0 && b !== 0 && c !== 0) {
            this.math(a, b, c);
        }
    }
    render() {
        return <App output={this.state.output} onChange={this.handleChange} />;
    }
}
